import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { API_URL } from '../constants/constants';
import { Observable, throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class LoginService {

  constructor(private http: HttpClient, private router: Router) { }

  private headers = new HttpHeaders({ 'Content-Type': 'application/json' });

  login(user: any): Observable<any> {
    console.log(API_URL + '/api/Login/LoginUser', user)
    return this.http.post<any>(API_URL + '/api/Login/LoginUser', user, { headers: this.headers }).pipe(
      retry(1),
      catchError(this.handleError)
    );
  }

  setSession(key: any, val: any): void {
    sessionStorage[key] = val;
  }

  setData(key: any, val: any): void {
    localStorage[key] = val;
  }

  getData(key: any): any {
    return localStorage[key];
  }

  logout() {
    localStorage.clear();
    this.router.navigate(['/']);
  }

  gotoNotFound() {
    this.router.navigate(['/403']);
  }

  isAuthenticated(): boolean {
    return localStorage.userID != null;
  }

  handleError(error: any) {
    return throwError(error.message || error);
  }

}
